let index_router = [{
  path: ["Welcome", "index"],
  name: "Welcome"
}, {
  path: ["User", "list"],
  name: 'UserList'
}, {
  path: ["System", "adminOne"],
  name: 'AdminOne'
}];

import index from './page/Index/index.vue'
import PublicLogin from './page/Public/login.vue'

let index_router_data = [];
for (let i in index_router) {
  let path = index_router[i].path.join("");
  let parameter = index_router[i].parameter ? index_router[i].parameter : "";
  let component = [];
  for (var k in index_router[i]['path']) {
    let obj = index_router[i]['path'][k];
    component.push(obj);
  }
  let data = {
    path: path + parameter,
    component: require('./page/Index/' + component.join("/") + '.vue'),
  }
  if (index_router[i].name) {
    data['name'] = index_router[i].name;
  }
  index_router_data.push(data);
}
export default [{
    path: '/',
    redirect: '/Index/WelcomeIndex',
  },
  {
    path: '/login',
    component: PublicLogin,
    meta: {
      requireAuth: true,
    },
  }, {
    path: '/Index',
    component: index,
    children: index_router_data
  }
]


