<template>
  <el-container>
    <!--  -->
    <el-aside width="150px">
      <router-link :to="{ name: 'Welcome'}" class="logo" title=" FALV">
        <i class="logobase64"></i>
        <span></span>
      </router-link>
      <el-menu router :default-active="routerurl" class="el-menu-vertical-demo" background-color="#1f2428" text-color="#fff" active-text-color="#ffd04b">
        <el-menu-item :index='$fun.searchMenuMap(["Index", "Userlist"])'>
          <i class="iconfont icon-people"></i>
          <span slot="title"></span>
        </el-menu-item>
        <el-menu-item index="2">
          <i class="iconfont icon-profile_light"></i>
          <span slot="title"></span>
        </el-menu-item>
        <el-menu-item index="3">
          <i class="iconfont icon-goods"></i>
          <span slot="title"></span>
        </el-menu-item>
        <el-menu-item index="4">
          <i class="iconfont icon-form_light"></i>
          <span slot="title"></span>
        </el-menu-item>
        <el-menu-item index="5">
          <i class="iconfont icon-vipcard"></i>
          <span slot="title"></span>
        </el-menu-item>
        <el-menu-item index="6">
          <i class="iconfont icon-settings_light"></i>
          <span slot="title"></span>
        </el-menu-item>
        <el-menu-item index="7">
          <i class="iconfont icon-comment_light"></i>
          <span slot="title"></span>
        </el-menu-item>
      </el-menu>
    </el-aside>
    <el-container>
      <!--  -->
      <el-header>
        <span v-on:click="$router.push('Welcome')"></span>
        <el-dropdown @command="handleCommand">
          <el-button type="text">
            {{curUser.username}}
            <i class="el-icon-arrow-down el-icon--right"></i>
          </el-button>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="myUser"></el-dropdown-item>
            <el-dropdown-item command="back"></el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </el-header>
      <!--  -->
      <router-view class="view"></router-view>
    </el-container>
  </el-container>
</template>
<script>
  export default {
    data() {
      return {
        routerurl: '',
      }
    },
    watch: {
      $route(route) {
        this.routeConfig();
      }
    },
    created: function () {
      this.routeConfig();
    },
    methods: {
      routeConfig() {
        var vm = this;
        setTimeout(() => {
          vm.routerurl = vm.$fun.getMenuIndexByRoute(vm.$route);
        }, 500);
      },
      handleCommand(command) {
        if (command == 'myUser') {
          this.$router.push({
            name: 'AdminOne'
          })
        } else if (command == 'back') {
          localStorage.setItem('loginState', false);
          this.$router.push('/login');
        }
      }
    }
  }
</script>


