<template>
  <el-main>
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ name: 'Welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card v-loading="loading">
      <div style="text-align:right;padding-bottom:15px;">
        <el-button type="danger" @click="dialogVisible = true"></el-button>
      </div>
      <el-form class="my-form" :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px">
        <el-form-item label="">
          <el-input v-model="ruleForm.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="ruleForm.email"></el-input>
        </el-form-item>
        <!-- <el-form-item label="" prop="phone">
          <el-input type="text" v-model="ruleForm.phone" :maxlength="11"></el-input>
        </el-form-item> -->
        <!-- <el-form-item label="">
          <el-input type="textarea" :autosize="{ minRows: 2, maxRows: 4}" :maxlength=1000 v-model="ruleForm.tag">
          </el-input>
        </el-form-item> -->
        <el-form-item label="">
          <el-row type="flex" justify="space-around">
            <el-button @click="$router.go(-1);"></el-button>
            <el-button type="primary" @click="submitForm()"></el-button>
          </el-row>
        </el-form-item>
      </el-form>
    </el-card>
    <editPassword :visible.sync="dialogVisible" :id="ruleForm.id" :apiUrl="api_admin_url" v-on:triggeredUpdates="getData"></editPassword>
  </el-main>
</template>
<script>
  import editPassword from "../../component/editPassword.vue"; //
  export default {
    components: {
      editPassword,
    },
    data() {
      return {
        loading: false,
        api_admin_url: '/Admino/admin/save.json',
        dialogVisible: false,
        ruleForm: {
          id: '',
          username: '',
          email: ''
          // phone: '',
        },
        rules: {
          username: [{
              required: true,
              message: '',
              trigger: 'blur'
            },
            {
              min: 3,
              max: 15,
              message: ' 3  15 ',
              trigger: 'blur'
            }
          ],
          // phone: [{
          //   required: true,
          //   message: '',
          //   trigger: 'blur'
          // }, {
          //   pattern: this.verifyRule.isPhone,
          //   message: '',
          //   trigger: 'blur'
          // }],
          email: [{
            required: true,
            type: 'email',
            message: '',
            trigger: 'blur'
          }],
        },
      }
    },
    created: function () {
      if (this.$route.query.id) {
        this.ruleForm.id = this.$route.query.id;
      } else {
        this.ruleForm.id = this.curUser.user_id;
      }
      this.getData()
    },
    methods: {
      submitForm() {
        this.$refs['ruleForm'].validate((valid) => {
          if (valid) {
            this.addData();
          }
        });
      },
      getData() {
        var vm = this;
        vm.loading = true;
        setTimeout(() => {
          vm.$http.get(vm.api_admin_url + '?id=' + vm.ruleForm.id)
            .then((response) => {
              if (response.data.code == 0) {
                // vm.ruleForm = response.data.data.data;
                vm.ruleForm.username = response.data.data.data.username;
                vm.ruleForm.email = response.data.data.data.email;
                this.loading = false;
              }
            })
        }, 500);
      },
      addData() {
        var vm = this;
        vm.loading = true;
        vm.$http.post(vm.api_admin_url, vm.ruleForm)
          .then((response) => {
            setTimeout(() => {
              if (response.data.code == 0) {
                vm.$message({
                  message: response.data.msg,
                  type: 'success'
                });
                // vm.$router.go(-1);
              } else {
                vm.$message.error(response.data.msg);
              }
              this.loading = false;
            }, 1000);
          })
      }
    }
  }
</script>


