<template>
    <el-dialog title="" :visible.sync="visible" center width="500px" :before-close="beforeClose">
        <el-form label-width="100px" :model="editForm" :rules="rules" ref="editForm">
            <el-form-item label="" prop="password">
                <el-input v-model="editForm.password" type="password"></el-input>
            </el-form-item>
            <el-form-item label="" prop="new_password">
                <el-input v-model="editForm.new_password" type="password"></el-input>
            </el-form-item>
            <el-form-item label="" prop="f_new_password">
                <el-input v-model="editForm.f_new_password" type="password"></el-input>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="beforeClose()"> </el-button>
            <el-button type="primary" @click="modifyPassword()" :loading="loading"> </el-button>
        </div>
    </el-dialog>
</template>
<script>
    export default {
        props: ["visible", "apiUrl", "id"],
        data() {
            var validatePass = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                } else {
                    if (this.editForm.f_new_password !== '') {
                        this.$refs['editForm'].validateField('f_new_password');
                    }
                    callback();
                }
            };
            var validatePass2 = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                } else if (value !== this.editForm.new_password) {
                    callback(new Error('!'));
                } else {
                    callback();
                }
            };
            return {
                loading: false,
                editForm: {
                    password: '', // 
                    new_password: '', // 
                    f_new_password: '', // 
                },
                rules: {
                    password: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    }],
                    new_password: [{
                        required: true,
                        validator: validatePass,
                        trigger: 'blur'
                    }],
                    f_new_password: [{
                        required: true,
                        validator: validatePass2,
                        trigger: 'blur'
                    }],
                }
            };
        },
        created: function () {},
        methods: {
            beforeClose() {
                this.$emit("update:visible", false);
            },
            modifyPassword() {
                var vm = this;
                vm.$refs['editForm'].validate((valid) => {
                    if (valid) {
                        vm.postData();
                    }
                });
            },
            postData() {
                var vm = this;
                vm.loading = true;
                setTimeout(() => {
                    vm.$http.post(vm.apiUrl, {
                        id: vm.id,
                        password: vm.editForm.new_password
                    }).then((response) => {
                        if (response.data.code == 0) {
                            vm.$message.success(response.data.msg);
                            vm.beforeClose();
                            // vm.$emit("triggeredUpdates");
                            vm.$refs['editForm'].resetFields();
                        } else {
                            vm.$message.error(response.data.msg);
                        }
                        vm.loading = false;
                    })
                }, 1000);
            }
        }
    }
</script>


