        methods: {
            submitForm() {
                this.$refs['LoginForm'].validate((valid) => {
                    if (valid) {
                        var vm = this;
                        vm.loading = true
                        setTimeout(() => {
                            vm.$http.post('/Admino/Login/login.json', vm.LoginForm)
                                .then((response) => {
                                    if (response.data.code == 0) {
                                        this.$message({
                                            message: response.data.msg,
                                            type: 'success'
                                        });
                                        this.getData();
                                    } else {
                                        this.$message.error(response.data.msg);
                                    }
                                    vm.loading = false;
                                })
                        }, 1000);
                    }
                });
            },
            getData() {
                var vm = this;
                vm.$http.get('/Admino/NAStaff/current.json')
                    .then((response) => {
                        if (response.data.code == 0) {
                            vm.$fun.setCurUser(response.data.data.think.user);
                            vm.login();
                        }
                    })
            },
            login() {
                //this.$fun.getAuth();
                if (this.$route.query.redirect && this.$route.query.redirect !== '/') {
                    this.$router.push(this.$route.query.redirect)
                } else {
                    this.$router.push({
