<template>
    <el-main>
        <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ name: 'Welcome' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <el-card v-loading="loading" style="min-height:600px;">
            <el-form :inline="true" label-width="80px">
                <el-form-item label="">
                    <el-input placeholder="" v-model.trim="parameter.search">
                        <i @click="routeTo" slot="suffix" class="el-input__icon el-icon-search"></i>
                    </el-input>
                </el-form-item>
                <router-link :to="{ name: 'AdminOne'}">
                    <el-button style="float:right" type="danger"> </el-button>
                </router-link>
            </el-form>
            <el-table border :data="listData.list_data">
                <el-table-column :label="value" :prop="key" v-for="(value,key) in listData.list_header" v-if="isShow(key)">
                </el-table-column>
                <el-table-column label="" width="260" center v-if="listData.list_data.length">
                    <template slot-scope="scope">
                        <el-button size="mini" @click="showItem=scope.row,dialogVisible = true"></el-button>
                        <el-button size="mini" :type="scope.row.is_black==''?'success':'danger'" @click="handleBlack(scope.row)">{{scope.row.is_black==''?'':''}}</el-button>
                        <el-button size="mini" type="warning"></el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div style="text-align: center;padding:20px 0;" v-if="listData.list_count!==0">
                <el-pagination @current-change="goPage" :page-size="10" :total="listData.list_count" :current-page="parseInt(parameter.page)"
                    layout="prev, pager, next, jumper">
                </el-pagination>
            </div>
        </el-card>
        <el-dialog title="" :visible.sync="dialogVisible" width="600px">
            <div v-for="(value,key) in listData.list_header">
                <span v-if="key!=='avatarUrl'">{{value}} : {{showItem[key]}}</span>
                <img v-else :src="showItem[key]?showItem[key]:'assets/images/defaultAvatar.png'" alt="" style="position:absolute;right:20px;top:90px;width:160px;">
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false"> </el-button>
                <el-button type="primary" @click="dialogVisible = false"> </el-button>
            </span>
        </el-dialog>
    </el-main>
</template>
<script>
    export default {
        data() {
            return {
                loading: false,
                listData: {
                    list_count: 0,
                    list_data: [],
                },
                showList: {
                    nickName: true,
                    type: true,
                    gender: true,
                    created_at: true,
                    is_black: true,
                },
                parameter: {
                    page: 1,
                    search: ''
                },
                dialogVisible: false,
                showItem: {}
            }
        },
        created: function () {
            this.queryStr();
        },
        computed: {

        },
        watch: {
            $route(route) {
                this.queryStr();
            }
        },
        methods: {
            // 
            isShow(item) {
                return this.showList[item] ? true : false
            },
            // 
            getData() {
                var vm = this;
                vm.loading = true;
                setTimeout(() => {
                    vm.$http.post('/Admino/User/index.json', vm.parameter)
                        .then((response) => {
                            if (response.data.code == 0) {
                                vm.listData = vm.filterData(response.data.data);
                            }
                            vm.loading = false;
                        })
                }, 1000);
            },
            // 
            handleBlack(row) {
                var type = 1;
                var text = ', PK?';
                if (row.is_black == '') {
                    type = 0;
                    text = ', ?';
                }
                this.$confirm(text, '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.editUserData(row.id, type);
                }).catch(() => {});
            },
            // ||
            editUserData(id, type) {
                var vm = this;
                vm.$http.post('/Admino/User/save.json', {
                    id: id,
                    is_black: type,
                }).then((response) => {
                    if (response.data.code == 0) {
                        vm.$message.success(response.data.msg);
                        vm.getData()
                    }
                })
            },
            filterData(data) {
                for (var item of data.list_data) {
                    item.type = item.type == 'SHADOW' ? '' : '';
                    item.is_black = item.is_black == '1' ? '' : '';
                    item.gender = item.gender == '1' ? '' : (item.gender == '2' ? '' : '');
                }
                return data;
            },
            queryStr() {
                if (this.$route.query) {
                    this.parameter = JSON.parse(JSON.stringify(this.$route.query));
                }
                this.getData();
            },
            routeTo() {
                this.$router.push({
                    name: 'UserList',
                    query: this.parameter
                })
            },
            goPage(val) {
                this.parameter.page = val;
                this.routeTo();
            },
        }
    }
</script>


