/*   
 * */
// menuindex   key: value:menu
var menuIndexMap = {
    Index: {
        Userlist: "/Index/Userlist?page=1", //
    }
};
export default {
    install: function (Vue, options) {
        Vue.prototype.webUrl = options.url;
        Vue.prototype.curUser = JSON.parse(localStorage.getItem('userInfo'));
        Vue.prototype.verifyRule = {
            isPhone: /^1(0|3|4|5|6|7|8|9)[0-9]\d{8}$/,
        };
        Vue.prototype.$fun = {
            // ...
            setCurUser: function (data) {
                Vue.prototype.curUser = data;
                localStorage.setItem('loginState', true);
                localStorage.setItem('userInfo', JSON.stringify(data));
            },
            searchMenuMap(indexArr, route) {
                var obj = menuIndexMap;
                for (var i = 0; i < indexArr.length; i++) {
                    if (!!obj[indexArr[i]] && (typeof obj === "object")) {
                        obj = obj[indexArr[i]];
                    } else {
                        return typeof obj === "string" ? obj : "/";
                    }
                }
                if (typeof obj === "function") {
                    obj = obj(route);
                }
                if (typeof obj === "object" && obj === menuIndexMap) {
                    console.log("MeunIndexNotRegister");
                    return "/";
                }
                return obj;
            },
            getMenuIndexByRoute(route) {
                var path = JSON.parse(JSON.stringify(route.path));
                var indexArr = path.split('/');
                indexArr.splice(0, 1);
                return this.searchMenuMap(indexArr, route);
            }
        }
        //  
        Vue.mixin({
            methods: {

                // 0
                zeroAdding: function (num) {
                    var tmp = num + '';
                    if (tmp.length <= 1) {
                        return tmp = "0" + tmp;
                    }
                    return num;
                },
                // 
                dataFormat(date, type) {
                    if (!date) return ''
                    if (type) {
                        return date.getFullYear() + '-' + this.zeroAdding((date.getMonth() + 1)) + '-' + this.zeroAdding(date.getDate()) + ' ' + this.zeroAdding(date.getHours()) + ':' +
                            this.zeroAdding(date.getMinutes()) + ':' + this.zeroAdding(date.getSeconds())
                    } else {
                        return date.getFullYear() + '-' + this.zeroAdding((date.getMonth() + 1)) + '-' + this.zeroAdding(date.getDate())
                    }
                },
                //  iOS safari
                getDateDiff(diffTime) {
                    //xxxx-xx-xx xxxx/xx/xx 
                    if (!!diffTime) {
                        return diffTime.replace(/\-/g, "/");
                    }
                    return '';

                },
                // 
                formatToDate(strDate, type) {
                    var tmp = new Date(this.getDateDiff(strDate));
                    if (tmp == 'Invalid Date') {
                        return '';
                    } else {
                        return tmp;
                    }
                }
            }
        })
    },
};


